export interface CellInterface {
    adds: string,
    name: string,
    img: string,
    coingeckoID: string,
    daiContract: string,
    pid: number,
    lpID: string,
    balance?: any
}

export const CellWidget: React.FC<{ type: string, adds: CellInterface[] }> = ({
                                                                                  type,
                                                                                  adds,
                                                                              }) => {


    return (
        <CellStyle>
            <h2>{type}Token Markets</h2>
            <h3>
                <p className={'one'}>{type}Token Markets</p>
                <p>Price</p>
                <p>My Liquidity</p>
                <p>LP APR</p>
                <p>Action</p>
            </h3>
            {adds.map((ev, i) => {

                return Items(type, adds, i);
            })
            }
            {adds.length === 0 && <div className={'loding'}>
                <div>No markets available</div>
            </div>}
        </CellStyle>
    );
}

const Items = (type: string, allData: CellInterface[], i: number) => {
    const data = allData[i]
    const prices = useGetPrice();
    const marketData = useMarketData(data.daiContract, data.adds);
    const {apy} = useUserInfo(data)


    return (<>


            <ItemStyle key={data.adds}>
                <div className={'one'}>
                    <div className="img">

                        <img src={data.img} className={'logo'} alt=""/>
                        <img src={type === 'd' ? DTokenIcon : ETokenIcon} className={'icon'} alt=""/>
                    </div>
                    <p>{data.name} {type}Token</p>
                </div>
                <p>$ {getBalanceFormat(marketData?.reserve1.toString() / marketData?.reserve0.toString() * prices[data.coingeckoID] * 1e18, 2)}</p>
                <p>$ {getBalanceFormat(data?.balance.toString() * prices[data.coingeckoID], 2)}</p>
                <p>{apy ?? '0'} %</p>
                <div className={'button'}>
                    <p className={'inner'} onClick={()=>{
                        window.open(`https://app.sushi.com/add/${marketData.token1}/${marketData.token0}`)
                    }}>Sell</p>
                    {/*<p className={'inner'}>Buy</p>*/}
                </div>
            </ItemStyle>

    </>);
}

