import {useActiveWeb3React} from "./index";
import {Contract} from '@ethersproject/contracts'
import {useCallback, useMemo} from "react";
import {getContract} from "../utils/contract_format";
import ERC20_ABI from '../constants/abis/erc20.json';
import CONV_ABI from '../constants/abis/conv.json';
import CHEF_ABI from '../constants/abis/chef.json';
import DAI_ABI from '../constants/abis/dai.json';
import LP_ABI from '../constants/abis/lp.json'
import Multisender_ABI from '../constants/abis/multisender.json'
import {
    ChainId, ChefContract,
    CONV_NETWORKS,
    LP_NETWORKS,
    MULTICALL_ABI,
    MULTICALL_NETWORKS,
    MULTISENDER_NETWORKS
} from "../constants/multicall";

function useContract(address: string | undefined, ABI: any, withSignerIfPossible = true): Contract | null {
    const {library, account, web3} = useActiveWeb3React()

    return useMemo(() => {

        if (!address || !ABI || !library || !web3.utils.isAddress(address)) return null

        try {

            return getContract(address, ABI, library, withSignerIfPossible && account ? account : undefined)
        } catch (error) {
            console.error('Failed to get contract', error)
            return null
        }
    }, [library, account, address])
}

export function useCbContract(): Function {
    const {library, account, web3} = useActiveWeb3React()

    return useCallback((address: string | undefined, ABI: any, withSignerIfPossible = true) => {

        if (!address || !ABI || !library || !web3.utils.isAddress(address)) return null

        try {

            return getContract(address, ABI, library, withSignerIfPossible && account ? account : undefined)
        } catch (error) {
            console.error('Failed to get contract', error)
            return null
        }
    }, [library, account])
}


export function useMulticallContract(): Contract | null {
    const {chainId} = useActiveWeb3React();
    return useContract(MULTICALL_NETWORKS[chainId ?? 42], MULTICALL_ABI, false)
}


export function useTokenContract(tokenAddress?: string, withSignerIfPossible?: boolean): Contract | null {
    return useContract(tokenAddress, ERC20_ABI, withSignerIfPossible);
}

export function useConvContract(): Contract | null {
    const {chainId} = useActiveWeb3React();
    return useContract(CONV_NETWORKS[chainId ?? 42], CONV_ABI);
}

export function useChefContract(): Contract | null {
    const {chainId} = useActiveWeb3React();
    return useContract(ChefContract[chainId ?? 42], CHEF_ABI.abi);
}

export function useDaiContract(address:string): Contract | null {
    return useContract(address, DAI_ABI.adi);
}

export function useLPContract(address: string): Contract | null {
    const {chainId} = useActiveWeb3React();
    return useContract(address, LP_ABI);
}



export function useMultisenderContract(withSignerIfPossible?: boolean): Contract | null {
    const {chainId} = useActiveWeb3React();
    return useContract(MULTISENDER_NETWORKS[chainId ?? 42], Multisender_ABI, withSignerIfPossible);
}

export function useBytes32TokenContract(tokenAddress?: string, withSignerIfPossible?: boolean): Contract | null {
    return useContract(tokenAddress, ERC20_BYTES32_ABI, withSignerIfPossible)
}

const ERC20_BYTES32_ABI = [
    {
        "constant": true,
        "inputs": [],
        "name": "name",
        "outputs": [
            {
                "name": "",
                "type": "bytes32"
            }
        ],
        "payable": false,
        "stateMutability": "view",
        "type": "function"
    },
    {
        "constant": true,
        "inputs": [],
        "name": "symbol",
        "outputs": [
            {
                "name": "",
                "type": "bytes32"
            }
        ],
        "payable": false,
        "stateMutability": "view",
        "type": "function"
    }
]
