import {useDaiContract} from "./useContract";
import {useEffect, useMemo, useState} from "react";
import {useSingleCallResult} from "../state/multicall/hooks";
import {useWeb3React} from "@web3-react/core";
import {useActiveWeb3React} from "./index";
import {CellInterface} from "../view/dashboard/cell_widget";


export const useMarketData = (address: string, tokenAddress: string) => {
    let data: any;
    const daiContract = useDaiContract(address);
    const {account} = useActiveWeb3React();
    const {result: reserves} = useSingleCallResult(daiContract, 'getReserves', []);
    const {result: token0} = useSingleCallResult(daiContract, 'token0', []);
    const {result: token1} = useSingleCallResult(daiContract, 'token1', []);


    return useMemo(() => {
        if (reserves && token0 && token1) {
            if (token0[0].toLowerCase() === tokenAddress.toLowerCase()) {
                data = {
                    reserve0: reserves?.[1],
                    reserve1: reserves?.[0],
                    blockTimestampLast: reserves?.[2].toString(),
                    token0: token0[0],
                    token1: token1[0],
                }
            } else {
                data = {
                    reserve0: reserves?.[0],
                    reserve1: reserves?.[1],
                    blockTimestampLast: reserves?.[2].toString(),
                    token0: token1[0],
                    token1: token0[0],
                }
            }

        }
        return data

    }, [reserves, token0])
}


