import React, {useMemo} from "react";
import styled from "styled-components";
import balanceBg from "../../assets/images/dashboard.png"
import BigNumber from "bignumber.js";
import {CellInterface} from "./cell_widget";
import {useGetPrice} from "../../state/price/hooks";
import {getBalanceFormat} from "../../utils/formatBalance";


export const BalanceWidget: React.FC<{ AllBalance: CellInterface[]  }> = ({AllBalance}) => {

    const prices = useGetPrice();
    let balance = useMemo(() => {
        let _tmp = new BigNumber(0);
        AllBalance.forEach((ev)=>{
            _tmp = _tmp.plus(ev.balance * prices[ev.coingeckoID])
        })
        return _tmp;
    }, [prices, AllBalance])

    return (
        <BalanceStyle>
            <h2>Balance</h2>
            <h3>$ {getBalanceFormat(balance, 2)}</h3>
        </BalanceStyle>
    );
}


const BalanceStyle = styled.div`
  width: 482px;
  height: 229px;
  margin-bottom: 60px;
  background: #FFFFFF;
  box-shadow: 0 2px 13px 0 rgba(153, 211, 189, 0.5);
  border-radius: 24px;
  padding: 43px 30px;
  box-sizing: border-box;
  background-image: url(${balanceBg});

  h2 {
    font-size: 24px;
    font-family: Rubik-Medium, Rubik;
    font-weight: 500;
    color: #569D92;
    line-height: 1;
    margin-bottom: 50px;
  }

  h3 {
    font-size: 24px;
    font-family: Rubik-Medium, Rubik;
    font-weight: 500;
    color: #333441;
    line-height: 1;
  }
`;
