import React, {useCallback, useEffect, useMemo, useState} from "react";
import {CellInterface, CellWidget} from "./cell_widget";
import {BalanceWidget} from "./balance_widget";
import {useActiveWeb3React} from "../../hook";
import {CONV_TOKENS} from "../../constants/multicall";
import {useCbContract} from "../../hook/useContract";
import DAI_ABI from "../../constants/abis/dai.json";
import BigNumber from "bignumber.js";
import {useGetPrice} from "../../state/price/hooks";
import {CircularProgress} from "@material-ui/core";
import styled from "styled-components";

export const DashBoardPage: React.FC = () => {
    const {chainId} = useActiveWeb3React();

    const {account} = useActiveWeb3React()
    const getContract = useCbContract()
    const [address, setAddress] = useState<any>({D: [], E: []});
    const [loding, setLoding] = useState(true);
    const prices = useGetPrice();

    const getBalances = useCallback(async () => {
        let dTokens: CellInterface[] = []
        let eTokens: CellInterface[] = [];
        for (const key in CONV_TOKENS) {

            let item = CONV_TOKENS[key];
            let dBalance = await getContract(item[chainId].dtoken, DAI_ABI.adi).balanceOf(account);
            let eBalance = await getContract(item[chainId].etoken, DAI_ABI.adi).balanceOf(account);

            if (dBalance.toString() !== '0') {
                dTokens.push({
                    adds: item[chainId].dtoken,
                    name: item.name,
                    img: item.img,
                    coingeckoID: item.coingeckoID,
                    lpID: item.lpID,
                    daiContract: item[chainId].ddaitoken,
                    pid: item.dpid,
                    balance: dBalance,
                })
            }
            if (eBalance.toString() !== '0') {
                eTokens.push({
                    adds: item[chainId].dtoken,
                    name: item.name,
                    img: item.img,
                    lpID: item.lpID,
                    balance: eBalance,
                    coingeckoID: item.coingeckoID,
                    daiContract: item[chainId].edaitoken,
                    pid: item.epid,
                })
            }
        }
        setAddress({D: dTokens, E: eTokens});
        setLoding(false)
    }, [account, prices])


    useEffect(() => {
        getBalances()
    }, [])


    return (
        <div>
            <BalanceWidget AllBalance={[...address.D, ...address.E]}/>

            {loding ? <LoadingWidget><CircularProgress/></LoadingWidget> : <div>
                <CellWidget type={"d"} adds={address.D}/>
                <CellWidget type={"e"} adds={address.E}/>
            </div>}
        </div>
    );
}

const LoadingWidget = styled.div`
  height: 100px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
`
