import {useDispatch, useSelector} from "react-redux";
import {AppDispatch, AppState} from "../index";
import {useCallback} from "react";
import {setPrice} from "./actions";
import {EthToUsd} from "../../net/api";
import price, {PriceInterface} from "./reducer";


export const useSetProce = () => {
    const dispatch = useDispatch<AppDispatch>();
    return useCallback(async () => {
        let result = await EthToUsd();
        for (let key in result) {
            result[key] = result[key].usd;
        }
        console.log(
            '----------------------run set price'
        )
        dispatch(setPrice({data: result}));
    }, [dispatch])
}


export const useGetPrice = (): PriceInterface => {
    const config = useSelector((state: AppState) => {

        return state.price.prices;
    })
    return config;
}
