import React, {useMemo} from "react";
import {CellInterface, CellWidget} from "./cell_widget";
import {CONV_TOKENS} from "../../constants/multicall"
import {useActiveWeb3React} from "../../hook";


export const MarketPage: React.FC = () => {
    const {chainId} = useActiveWeb3React();

    const [D, E] = useMemo(() => {
        let dTokens: CellInterface[] = []
        let eTokens: CellInterface[] = []
        for (const key in CONV_TOKENS) {
            let item = CONV_TOKENS[key];
            dTokens.push({
                adds: item[chainId].dtoken,
                name: item.name,
                img: item.img,
                coingeckoID: item.coingeckoID,
                lpID: item.lpID,
                daiContract: item[chainId].ddaitoken,
                pid: item.dpid,
            })

            eTokens.push({
                adds: item[chainId].dtoken,
                name: item.name,
                img: item.img,
                lpID: item.lpID,

                coingeckoID: item.coingeckoID,
                daiContract: item[chainId].edaitoken,
                pid: item.epid,
            })
        }
        return [dTokens, eTokens]
    }, [CONV_TOKENS, chainId]);
    return (<div>
        <CellWidget type={"d"} adds={D}/>
        <CellWidget type={"e"} adds={E}/>
    </div>);
}


