import {
    HashRouter as Router,
    Switch,
    Route,
    Link, Redirect
} from "react-router-dom";
import React, {useCallback, useEffect} from "react";
import {AppBar} from "./widget/app_bar";
import styled from "styled-components";
import {MarketPage} from "./view/market/market";
import {DashBoardPage} from "./view/dashboard/dashboard";
import {useSetProce} from "./state/price/hooks";


export default function RouterPage() {
    const setPrice = useSetProce();
    useEffect(() => {
        setPrice()
    }, [])


    return (
        <Router>
            <div>
                <AppBar/>

                <MainStyle>
                    <Switch>

                        <Route path="/market">
                            <MarketPage/>
                        </Route>
                        <Route path="/dashboard">
                            <DashBoardPage/>
                        </Route>
                        <Redirect to="/market"/>

                    </Switch>
                </MainStyle>

            </div>
        </Router>
    );
}


const MainStyle = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 59px;
`

