import {useCallback, useEffect, useMemo, useState} from "react";
import BigNumber from "bignumber.js";
import {useActiveWeb3React} from "./index";
import {useChefContract, useLPContract} from "./useContract";
import {CellInterface} from "../view/market/cell_widget";
import {useGetPrice} from "../state/price/hooks";


export const useUserInfo = (ev: CellInterface) => {

    const {account} = useActiveWeb3React();
    const {adds, daiContract: lpAddr, pid, lpID} = ev;

    const chefContract = useChefContract()
    const prices = useGetPrice();

    const [balance, setBalance] = useState<any>({

        apy: null
    });
    const lpContrcts = useLPContract(lpAddr);


    const facethBack = useCallback(async () => {
        if (!chefContract || !lpContrcts) return null
        const otherTokenPrice = prices[lpID];
        if (!otherTokenPrice) {
            return null;
        }


        let {allocPoint, amount} = await chefContract.poolInfos(pid);

        if (!amount || amount.toString() === "0") {
            return null;
        }

        let totalAllocPoint = await chefContract.totalAllocPoint()
        let totalSupply = await lpContrcts.totalSupply();
        let token1: string = await lpContrcts.token1();
        let reward = await chefContract.reward();
        let period = await chefContract.period();

        const {
            _reserve0,
            _reserve1,
        } = await lpContrcts.getReserves();

        let rewardInUsd = new BigNumber(reward.toString()).times(allocPoint.toString())
            .div(period.toString())
            .div(totalAllocPoint.toString())
            .times(365 * 24 * 60 * 60)
            .times((prices["sunder-goverance-token"]))
        ;
        // rewardInUsd = rewardInUsd.mul(0.0100);
        // reward.mul(allocPoint).mul(0.0100);


        let daiAmount;
        if (token1.toLowerCase() === adds.toLowerCase()) {
            daiAmount = _reserve0;
        } else {
            daiAmount = _reserve1;
        }
        let poolInUsd = new BigNumber(amount.toString()).times(daiAmount.toString()).times(2 * otherTokenPrice).div(totalSupply.toString());
        const apy = rewardInUsd.div(poolInUsd.toString());
        setBalance({
            apy:apy.toFixed(2),
        });


    }, [account, pid, chefContract, lpContrcts, prices]);

    useEffect(() => {
        facethBack();
    }, [
        prices,
        chefContract,
        lpContrcts
    ])

    return balance;
}
